@font-face {
    font-family: "HelveticaNeueReg";
    font-display: swap;
    src: url("../assets/fonts/HelveticaNeueMEforSKYW21-Rg_web.woff") format("woff");
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: "HelveticaNeueBold";
    font-display: swap;
    src: url("../assets/fonts/HelveticaNeueW23forSKY-Bd.woff") format("woff");
    font-weight: 800;
    font-style: normal;
}

body,html {
    font-family: "HelveticaNeueReg";
    width: 100%;
    height: 100%;

    color: #000;
    line-height: 1.5;
}
strong {
    font-family: "HelveticaNeueBold";
}
a {
    text-decoration: none;
    outline: none;
}
a:hover,
a:focus{
    text-decoration: underline;
    outline: 3px solid red;
}
img, video {
    max-width: 100%;
    height: auto;
}
.video_full video{
    height: 100%;
    width: 100%;;
}

.video_full{
    background-color:silver;
    background-image: url("../assets/images/app_bg.png");
    background-size: cover;
    background-repeat: no-repeat;
}
.hide{
    display: none !important;
}
.visibility_hidden,
.visibility_hidden *{
    visibility: hidden !important;
}
.page_content {
    background-color:silver;
    background-image: url("../assets/images/app_bg.png");
    background-size: cover;
    background-repeat: no-repeat;
}

.page_content .focusable:focus,
.page_content .focusable:focus-visible,
.page_content .focusable:hover {
    outline:none;
    outline:1px solid red;
    box-shadow: 0px 0px 4px 4px rgba(0, 0, 0, 0.15);
}
